import React, { Component } from 'react';

export default class Splash extends Component {
  render() {
    return (
      <div className="splash">
        <h1>Mallory Furniture</h1>
        <p>Your furniture is old.</p>
        <p>Ours is older.</p>
      </div>
    );
  }
}



