import React, { Component } from 'react';

const NotFound = () =>
<section>
  <div className="content block-text">
    <h1>404 page not found</h1>
    <article>
      <p>We are sorry but the page you are looking for does not exist.</p>
    </article>
  </div>
</section>

export default NotFound;



